import React from 'react'

function DisplayErro({ erro }) {

    if (erro.networkError) {
        return (
            <div>
                Voc parece estar offline, alguns recursos podem no estar disponveis.
            </div>
        );
    } else {
        return (
            <div>
                Algum tipo de erro ainda desconhecido foi lanado. Verificar logs.
            </div>
        )
    }


}

export default DisplayErro;